.Header{
    border: 1px solid #cccccc;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    background-color: #FAFAFA;
    border-radius: 5px;
}

.Header__logo span{
    font-weight: 300;
    font-style: italic;
}

.Header__ul{
    display: flex;
    flex-direction: row;
    list-style: none;
    gap: 1rem;
}

.Header__ul a{
    text-decoration: none;
    color: black;
    font-weight: 600;
    cursor: pointer;
    padding: 5px;
    border-radius: 3px;
    font-size: 1.3rem;
}

.Header__ul a:hover{
    background-color: #1CB5E0;
    color: #FAFAFA;
}

.Header__admin{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: 200;
}

/* ------------- switch ------------- */

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }